import { StyledGrid } from "../styles";
import Tooltip from "@mui/material/Tooltip"; 
import Fade from '@mui/material/Fade';

const desc = new Map([
    ["Acoustique", "Dcrit  quel point une piste est accoustique"],
    ["Dansabilit", "Dcrit  quel point une piste est adapte  la danse en fonction d'une combinaison d'lments musicaux, notamment le tempo, la stabilit du rythme, la force des battements et la rgularit gnrale."],
    ["nergie", "Dcrit le taux d'nergie d'une piste. En rgle gnrale, les morceaux nergiques sont rapides, forts et bruyants. Par exemple, le death metal a une nergie leve, tandis qu'un prlude de Bach est bas sur l'chelle."],
    ["Instrumentalit", "Dcrit si une piste ne contient pas de voix. Les sons \"Ooh\" et \"aah\" sont traits comme instrumentaux dans ce contexte. Les morceaux de rap ou de crations orales sont clairement \"vocaux\"."],
    ["Valence", "Dcrit la positivit musicale vhicule par un morceau"]
])

const StatGrid = ({stats}) => (
    <>
        {stats ? (
            <StyledGrid>
                {Array.from(stats).map(([key, value]) => (
                    <Tooltip title={desc.get(key)} TransitionComponent={Fade} followCursor key={key}>
                        <li className="grid_item" key={key}>
                            <div className="grid_item_inner">
                                    <p className="grid_item_stat_value">{value}</p>
                                    <h3 className='grid_item_stat_name'>{key}</h3>
                            </div>
                        </li>
                    </Tooltip>
                ))}
            </StyledGrid>
        ): (
        <p className='empty-notice'>Pas de stats  afficher </p>
        )}
    </>
)

export default StatGrid;